<script>
import LeadDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modaldeploy from "../../../components/Common/Modal/modaldeploy.vue";
import crmreadmore from "../../../components/Common/Modal/crm-readmore.vue";
import $ from "jquery";
export default {
  components: {
    crmtimeline,
    crmnote,
    crmattachment,
    modaltask,
    modalattachment,
    modalevent,
    modalcall,
    modaldeploy,
    crmreadmore
  },
  data() {
    return {
      activeKey: 1,
      isActivities: false,
      isNotes: false,
      isAttachment: false,
      iscontactDetails: null,
      showContactModal: false,
      showProductModal: false,
      showAddTaskModal: false,
      showAddEventModal: false,
      showCallModal: false,
      sendEmailModal: false,
      showAttachmentModal: false,
       LeftViewData: [],
      noteData: [],
      RightViewData: null,
      attachmentData: [],
      activitiesData: null,
      timelinedata: [],
      productData: null,
      productHeaders: null,
      contactsData: null,
      contactsHeader: null,
      noRecordForTab: false,
      isShowNoteDialogue: false,
      leadsHeader: null,
      dealsHeader: null,
      dealsData: null,
      leadsData: null,
      EmailData: [],
      template1: false,
      template2: false,
      template3: false,
      template4: false,
      SummaryData: [],
      TemplateName: null,
      CRMInvoice: false,
      modalItem: null,
      ginNumber: "--",
      noteText: "",
      SortBy: "",
      SortExp: "",
      PageNumber: 1,
      PageSize: 50,
      modalTaskItem: null,
      modalFollowUpItem: null,
      modalCallItem: null,
      modalDeleteItem: null,
      isView: false,
      modal: null,
      stausName: "",
      showAddEmailTemplate: false,
      showDeployModal: false,
      LocationSelect: null,
      ginid: null,
      ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
      editUrl: `/CRM/ManageLeadEdit/${this.$route.params.id}/YES/CRM/InwardListing`,
      userid: null,
      companyId: null,
      containername: null,
      email: null,
      NoteListType: "list",
      isShowNotePopup: false,  
      privilegeParams: null,
      isLoading:false
    };
  },
  created: function () {
      this.userid= this.GetUserInfo.ID;
      this.companyId= this.GetUserInfo.CompanyId;
      this.containername= this.GetUserInfo.ContainerName;
      this.email= this.GetUserInfo.Email;
      this.privilegeParams = `controller=CRM&action=`;
      this.FetchData();
  },
  updated(){
  var vm = this; 
      setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);
     $("#ancActivityExpandAllCollapseAll").click(function () {
      if ($(this).attr("show") == "0") 
    {$(this).attr("show", "1");
     $("#divconversationbox").find("#accordion").find('.collapse').addClass('show'); }
     else { 
        $(this).attr("show", "0"); 
     $("#divconversationbox").find("#accordion").find('.collapse').removeClass('show')
    }
  });
},
  methods: {
    FilterDDLandNormalValue: function (item) {
      if (item && item.length > 0) {
        try {
          return JSON.parse(item)[0].name;
        } catch (ex) {
          return "";
        }
      } else {
        return "";
      }
    },
    FilterDDLValueForID: function (item) {
      if (item.length > 0) {
        try {
          if (typeof JSON.parse(item)[0] == "object") {
            return JSON.parse(item)[0].value;
          } else {
            return JSON.parse(item)[0];
          }
        } catch (ex) {
          return "";
        }
      } else {
        return "";
      }
    },
    DisableButton: function () {
      var vm = this;
      vm.isActivities = false;
      vm.isNotes = false;
      vm.isAttachment = false;
    },
    checkForAdditionalPermission: function (TypeCode) {
      var rtrn = true;
      if (
        this.additionalOwnerPermission != null &&
        this.additionalOwnerPermission.length > 0
      ) {
        var prmsn = this.additionalOwnerPermission.filter(function (c) {
          return c.permission_code == TypeCode;
        });
        if (prmsn.length == 0) {
          rtrn = false;
        }
      }
      return rtrn;
    },
    formatDate: function (value) {
      if (value) {
        return moment(
          value,
          String(Globalize.culture().calendar.patterns.d).toUpperCase()
        ).format(String(Globalize.culture().calendar.patterns.d).toUpperCase());
      }
    },

    openlocation: function (value) {
      var vm = this;
      LeadDataService.CheckPrivilege(vm.privilegeParams + 'ApproveInward')
              .then(function (response) {
                if (response.data) {
                  if (
                    $(event.target).hasClass("disabled") ||
                    $(event.target).closest("a.iconenable").hasClass("disabled")
                  ) {
                    return false;
                  }
                  vm.showDeployModal = vm.showDeployModal ? false : true;
                  vm.ginid = value;
                  setTimeout(function () {
                  
                  }, 500);
           
                }
                else {
                  vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                }
  });
       
    },
    commonApprove: function (LocationSelect) {
      this.LocationSelect = LocationSelect;
      if (this.LocationSelect != null) {
        var vm = this;
        var val = "";
        if (typeof this.ginid !== "undefined" && this.ginid != null) {
          val = this.ginid;
        } else {
          $(".chkItems:checkbox:checked")
            .not("[id^='chkAll']")
            .each(function () {
              if (val.length > 0) val += ",";
              val += $(this).val();
            });
        }
        if (val.length > 0 || val != "") {
          vm.confirmR(vm.$t("AreYouSureToApproveSelectedRecords"),true,null,function (result) {
                var postJSON = {
                  ids: val,
                  moduleName: "CRM",
                  subModuleCode: "CRM_INWARD",
                  approvaltype: "CRM_APPROVE",
                  viewmode: "YES",
                  locationid: vm.LocationSelect,
                };
                var objectPostString = JSON.stringify(postJSON);
                LeadDataService.changestausInwards(objectPostString).then(
                  function (response) {
                    //response.headers.get('Expires');
                    var box = "";
                    if (response.data[0].Status == "Success") {
                      $(".chkItems:checkbox:checked")
                        .prop("checked", false)
                        .removeAttr("checked");
                      box = vm.ShowAlert("GIN Inward Approved Successfully",
                        "success",
                        true,
                        vm.$t("Alert")
                      );
                      vm.loadData("products");
                    } else {
                      box = vm.ShowAlert(vm.$t("Error Occur while Approving Record"));
                    }
                    setTimeout(function () {
                      box.modal("hide");
                    }, 2000);                     
                  }
                );           
            }
          );
        } else {
          vm.ShowAlert(vm.$t("LeaveApprove"));
        }
      } else {
        vm.ShowAlert("Please Select the location in order to approve");
      }
    },
    commonReject: function (id) {
         var vm= this;
        LeadDataService.CheckPrivilege(vm.privilegeParams + 'RejectInward')
              .then(function (response) {
                if (response.data) {
                  if (
                    $(event.target).hasClass("disabled") ||
                    $(event.target).closest("a.iconenable").hasClass("disabled")
                  ) {
                    return false;
                  }

                  var val = "";
                  if (typeof id !== "undefined" && id != null) {
                    val = id;
                  } else {
                    $(".chkItems:checkbox:checked")
                      .not("[id^='chkAll']")
                      .each(function () {
                        if (val.length > 0) val += ",";
                        val += $(this).val();
                      });
                  }
                  if (val.length > 0 || val != "") {
                    vm.confirmR(vm.$t("AreYouSureToRejectSelectedRecords"),true,null, function (result) { 
                      var postJSON = {
                        ids: val,
                        moduleName: "CRM",
                        subModuleCode: "CRM_INWARD",
                        approvaltype: "CRM_REJECT",
                        viewmode: "YES",
                        locationid: "",
                      };
                      var objectPostString = JSON.stringify(postJSON);
                      LeadDataService.changestausInwards(objectPostString).then(function (
                        response
                      ) {
                       if (response.data[0].Status == "Success") {
                          $(".chkItems:checkbox:checked").prop("checked", false).removeAttr("checked");
                          vm.ShowAlert((vm.$t("GinInward") + vm.$t("RejectSuccess")),"success",true,"Alert");
                          vm.loadData("products");
                        } else {
                          vm.ShowAlert(vm.$t("RejectError",[vm.$t("GinInwardType")]),"failure" ,true, "Alert");
                        }
                      });
                    });
                  } else {
                    vm.ShowAlert(vm.$t("RemoveReject"));
                  }
                }
                else {
                  vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                }
  });
    },
    openCRMAttachmentModal: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAttachment")
      ) {
        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.showAttachmentModal =
          this.showAttachmentModal ? false : true;
      }
    },
    FetchData: function () {
      var vm = this;
     vm.isLoading=true;
      LeadDataService.FormLayoutView(vm.ApiURL).then(function (response) {
        vm.isLoading=false;
        var fullLeftJSON = response.data.LEFTDATA;
        fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
          return element.form_field_visibility == "YES";
        });
        var stausName = $.grep(fullLeftJSON, function (element, index) {
          return element.name == "status_id";
        });
        if (stausName != null && stausName.length > 0) {
          this.stausName = stausName[0];
        }
      
        if (
          response.data.RIGHTDATA != null &&
          response.data.RIGHTDATA.length > 0
        ) {
          var fullJSON = response.data.RIGHTDATA;
          var gruopBy = "";
          $.each(
            fullJSON,
            $.proxy(function (index, item) {
              gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
              var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                return element.groupKey == gruopBy;
              });
              if (chkGroup.length == 0) {
                var returnedData = $.grep(fullJSON, function (element, index) {
                  return (
                    vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy
                  );
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    groupKey: gruopBy,
                    groupData: returnedData,
                  };
                  vm.timelinedata.push(objGroup);
                }
              }
            }, vm)
          );
        
          vm.noRecordForTab = false;
        } else {
          vm.noRecordForTab = true;
        }
        if (fullLeftJSON != null && fullLeftJSON.length > 0) {
          var groupId = fullLeftJSON[0].group_id;
          $.each(
            fullLeftJSON,
            $.proxy(function (index, item) {
          
              groupId = item.group_id;
              if (item.name == "gin_number") {
                vm.ginNumber = item.value;
              }
              var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                return element.group_id == groupId;
              });
              if (chkGroup.length == 0) {
                var returnedData = $.grep(
                  fullLeftJSON,
                  function (element, index) {
                    return element.group_id == groupId;
                  }
                );
                if (returnedData.length > 0) {
                  var objGroup = {
                    group_id: groupId,
                    group_name: returnedData[0].group_name,
                    LeftViewData: returnedData,
                  };
                  vm.LeftViewData.push(objGroup);
                }
              }
             }, vm)
          );
          vm.isLoading=false;
        }
        setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
        setTimeout(function () { vm.SetResponsiveTab() }, 200);
        setTimeout(function () { vm.RadioButtonBootstrap() }, 500);
      });
    },
    changeDateForAddNoteUsrEnd: function (value) {
      try {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
        var getDate = new Date(
          moment(String(value)).year(),
          moment(String(value)).month(),
          moment(String(value)).date(),
          moment(String(value)).hours(),
          moment(String(value)).minutes()
        );
        if (userTimeZone[0].indexOf("+") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() - parseInt(userTimeZone[0].replace("+", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() - parseInt(userTimeZone[1]))
          );
        } else if (userTimeZone[0].indexOf("-") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() + parseInt(userTimeZone[0].replace("-", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() + parseInt(userTimeZone[1]))
          );
        }
        return moment(getDate, "YYYY-MM-DD HH:mm").format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
            " " +
            String(timeFormat)
        );
      } catch (e) {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
        return moment(String(value)).format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
            " " +
            String(timeFormat)
        );
      }
    },
    openNoteDialogue() {
      this.isShowNoteDialogue = this.isShowNoteDialogue ? false : true;
    },
    openEmailModal(item) {
      this.sendEmailModal = true;
      this.modalItem = item;
    },
    openAddTaskModel() {
      this.showAddTaskModal = this.showAddTaskModal ? false : true;
    },
    openAddNewEvent() {
      this.showAddEventModal = this.showAddEventModal ? false : true;
    },
    openCallEvent() {
      this.showCallModal = this.showCallModal ? false : true;
    },
    openActivityModal(item) {
   
      if (item.activity_type == "Task") {
        this.showAddTaskModal = true;
        this.modalTaskItem = item;
      } else if (item.activity_type == "FollowUp") {
        this.showAddEventModal = true;
        this.modalFollowUpItem = item;
      } else if (item.activity_type == "Call") {
        this.showCallModal = true;
        this.modalCallItem = item;
      }
    },
    DeleteActivity(item) {
      this.modalDeleteItem = item;
      this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
    },
    DeleteActivityData: function (id) {

var vm = this;
if (id > 0) {
  vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
  
    var postJSON = {
      ids: id,
      moduleName: "CRM",
      subModuleCode: "CRM_INWARD",
      refCode: "DELETE_VIEW_ACTIVITY",
      refId: vm.sourceId
    };
    var objectPostString = JSON.stringify(postJSON);
  
    LeadDataService.CommonDelete(objectPostString).then(
      function (response) {
  if (response.data[0].Status == "Success") {
        
          vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t('Activity')]),"success", true, vm.$t("Alert"));
          vm.loadData("activities");
        } else {
    vm.ShowAlert(vm.$t("DeletedError", [vm.$t('Activity')]), "success", true,vm.$t("Alert"));
        }
         
      }
    );

  });
}
},
    openContactModal(contactDetails) {
      this.iscontactDetails = contactDetails;
      this.showContactModal = this.showContactModal ? false : true;
    },
    openShowNotePopup: function () {
      var ths = this;
      if (
        typeof ths.checkForAdditionalPermission !== "undefined" &&
        !ths.checkForAdditionalPermission("AddNote")
      ) {
        vm.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        ths.isShowNotePopup = true;
        $("#tab-notes").trigger("click");
        ths.loadData("notes");
      }
    },
    deleteContactFromTab(value) {
      var vm = this;
      if (value > 0) {
        vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
          var postJSON = {
            ids: value,
            moduleName: "CRM",
            subModuleCode: vm.$route.params.subModuleCode,
            refCode: "DELETE_VIEW_CONTACTS",
            refId:   vm.$route.params.id,
          };
          var objectPostString = JSON.stringify(postJSON);
          LeadDataService.CommonDelete(objectPostString).then(function (
            response
          ) {
            var box = "";
            if (response.data[0].Status == "Success") {
              $(".chkItems:checkbox:checked")
                .prop("checked", false)
                .removeAttr("checked");
              vm.loadData("contacts");
              box = vm.ShowAlert(vm.$t("DeletedSuccess"), vm.$t("Contact"));
            } else {
              box = ShowAlert(vm.$t("DeletedError"), vm.$t("Contact"));
            }
            setTimeout(function () {
              box.modal("hide");
            }, 2000);
             
          });
        });
      } else {
        vm.ShowAlert(vm.$t("DeletedError"), vm.$t("Contact"));
      }
    },
    formatDateTime(value) {
      if (value) {
        try {
          var timeFormat = Globalize.culture().calendar.patterns.t;
          timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
          var getDate = new Date(
            moment(String(value)).year(),
            moment(String(value)).month(),
            moment(String(value)).date(),
            moment(String(value)).hours(),
            moment(String(value)).minutes()
          );
          return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(
            String(Globalize.culture().calendar.patterns.d).toUpperCase() +
              " " +
              String(timeFormat)
          );
        } catch (e) {
          var timeFormat = Globalize.culture().calendar.patterns.t;
          timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
          return moment(String(value)).format(
            String(Globalize.culture().calendar.patterns.d).toUpperCase() +
              " " +
              String(timeFormat)
          );
        }
      }
    },
    loadData: function (tabName, callFrom) {
      var vm = this;
      vm.DisableButton();
      if (tabName == "notes") {
        vm.isNotes = true;
        vm.activeKey = 2;
        if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
      } else if (tabName == "attachments") {
        vm.isAttachment = true;
        vm.activeKey = 3;
      } else if (tabName == "activities") {
        vm.isActivities = true;
        vm.activeKey = 4;
      }
      if (tabName != "" && tabName != null) {
         

        if (tabName == "attachments") {
          LeadDataService.AttachmentListing(vm.ApiURL).then(function (
            response
          ) {
             
            if (response.data != null) {
              vm.attachmentData = response.data.data;
              setTimeout(vm.CheckBoxBootstrap(), 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "notes") {
          vm.noteData = [];
          LeadDataService.NoteListing(vm.ApiURL).then(function (response) {
             
            if (response.data != null && response.data.data.length > 0) {
              vm.noteData = response.data.data;
              setTimeout(vm.CheckBoxBootstrap(), 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "activities") {
          LeadDataService.ActivityListing(vm.ApiURL).then(function (response) {             
            if (response.data != null && response.data.data.length > 0) {
              vm.activitiesData = response.data.data;
              setTimeout(vm.CheckBoxBootstrap(), 100);
              setTimeout(vm.ResponsiveDataTable('table-openactivities-content'),500);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "products") {    
          LeadDataService.GetGoodInwardAssociatedProducts(vm.ApiURL).then(
            function (response) {               
              if (response.data != null && response.data.data.length > 0) {
                vm.productData = response.data.data;
                setTimeout(vm.CheckBoxBootstrap(), 100);
                setTimeout(vm.ResponsiveDataTable('table-product-content'), 500);
                vm.noRecordForTab = false;
              } else {
                vm.noRecordForTab = true;
                vm.productData = null;
              }
            }
          );
          vm.activeKey = 5;
        } else if (tabName == "timeline") {
          LeadDataService.TimelineListing(vm.ApiURL).then(function (response) {
             
            if (response.data != null && response.data != "") {
              vm.timelinedata = [];
              var fullJSON = response.data.data;
              var gruopBy = "";
              $.each(
                fullJSON,
                $.proxy(function (index, item) {
                  gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                 
                  var chkGroup = $.grep(
                    vm.timelinedata,
                    function (element, index) {
                      return element.groupKey == gruopBy;
                    }
                  );
                  if (chkGroup.length == 0) {
                
                    var returnedData = $.grep(
                      fullJSON,
                      function (element, index) {
                        return (
                          vm.$options.filters.formatDate(element.Complete_datetime) ==
                          gruopBy
                        );
                      }
                    );
                    if (returnedData.length > 0) {
                      var objGroup = {
                        groupKey: gruopBy,
                        groupData: returnedData,
                      };
                      vm.timelinedata.push(objGroup);
                      window.location.reload();
                    }
                  }
                }, vm)
              );
              setTimeout(vm.CheckBoxBootstrap(), 100);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
              window.location.reload();
            }
          });
          vm.activeKey = 1;
        }
      }
    },
  },

};
</script>
<template>
    <div id="divMainFormData" v-cloak>
      <loader :is-visible="isLoading"></loader>
      <section id="main-content" class="main-content">      
          
            <div class="left-menu-tab div-click-event">
              <div class="col-md-12 p-0">
                <div class="theme-primary partition">
                  <span class="p-name text-white textelipsis">{{ginNumber}}</span>
                </div>
              </div>
              <ul
                class="nav nav-pills flex-column left-tab mt-2 responivecustomtab"
                id="myTab"
                role="tablist"
              >
                <li class="nav-item">
                  <a
                    class="nav-link left-menu"
                    :class="{ 'active': activeKey == 1 }"
                    data-bs-target="#tab-lead-content"
                    href="javascript:void(0)"
                    data-bs-toggle="tab"
                    role="tab"
                    id="tab-Details"
                    v-on:click="loadData('timeline'); DisableButton()"
                  >
                    <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>
                    {{ $t('Detail') }}
                  </a>
                </li>
                <li class="nav-item" v-if="checkForAdditionalPermission('ViewNotes')">
                  <a
                    class="nav-link left-menu"
                    :class="{ 'active': activeKey == 2 }"
                    data-bs-target="#tab-notes-content"
                    href="javascript:void(0)"
                    id="tab-notes"
                    data-bs-toggle="tab"
                    role="tab"
                    v-on:click="loadData('notes')"
                  >
                    <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>
                    {{ $t('NOTE') }}
                  </a>
                </li>
                <li class="nav-item" v-if="checkForAdditionalPermission('ViewAttachment')">
                  <a
                    class="nav-link left-menu"
                    :class="{ 'active': activeKey == 3 }"
                    data-bs-target="#tab-attachment-content"
                    href="javascript:void(0)"
                    id="tab-attachment"
                    data-bs-toggle="tab"
                    role="tab"
                    v-on:click="loadData('attachments')"
                  >
                    <em class="fa fa-file" aria-hidden="true" aria-selected="false"></em>
                    {{ $t('Attachments') }}
                  </a>
                </li>
                <li class="nav-item" v-if="checkForAdditionalPermission('ViewAcitvities')">
                  <a
